import React, { useState } from "react";

export default function Registration(props) {
    const [state, setState] = useState({
        show_log: true,
    })
    return (
        <div className="main-register-wrap modal" style={{ display: props.show_reg }}>
            <div className="reg-overlay" onClick={props.close} style={{ display: props.show_reg }}></div>
            <div className="main-register-holder">
                <div className="main-register fl-wrap">
                    <div className="close-reg color-bg" onClick={props.close}><i className="fal fa-times"></i></div>
                    <ul className="tabs-menu">
                        <li className={state.show_log ? "current" : ""} onClick={() => setState({ ...state, show_log: true })}><a href="#tab-1"><i className="fal fa-sign-in-alt"></i> Login</a></li>
                        <li className={state.show_log ? "" : "current"} onClick={() => setState({ ...state, show_log: false })}><a href="#tab-2"><i className="fal fa-user-plus"></i> Register</a></li>
                    </ul>
                    <div id="tabs-container">
                        <div className="tab">
                            <div id="tab-1" className="tab-content" style={state.show_log ? { display: "" } : { display: "none" }}>
                                <h3>Sign In <span>Blue<strong>Ribons</strong></span></h3>
                                <div className="custom-form">
                                    <form method="post" name="registerform">
                                        <label>Username or Email Address <span>*</span> </label>
                                        <input name="email" type="text" onClick="this.select()" value="" />
                                        <label >Password <span>*</span> </label>
                                        <input name="password" type="password" onClick="this.select()" value="" />
                                        <button type="submit" className="log-submit-btn color-bg"><span>Log In</span></button>
                                        <div className="clearfix"></div>
                                        <div className="filter-tags">
                                            <input id="check-a" type="checkbox" name="check" />
                                            <label for="check-a">Remember me</label>
                                        </div>
                                    </form>
                                    <div className="lost_password">
                                        <a href="#">Lost Your Password?</a>
                                    </div>
                                </div>
                            </div>
                            <div className="tab">
                                <div id="tab-2" className="tab-content" style={state.show_log ? { display: "" } : { display: "block" }}>
                                    <h3>Sign Up <span>Blue<strong>Ribons</strong></span></h3>
                                    <div className="custom-form">
                                        <form method="post" name="registerform" className="main-register-form" id="main-register-form2">
                                            <label >Full Name <span>*</span> </label>
                                            <input name="name" type="text" onClick="this.select()" value="" />
                                            <label>Email Address <span>*</span></label>
                                            <input name="email" type="text" onClick="this.select()" value="" />
                                            <label >Password <span>*</span></label>
                                            <input name="password" type="password" onClick="this.select()" value="" />
                                            <label >Confirm Password <span>*</span></label>
                                            <input name="password" type="password" onClick="this.select()" value="" />
                                            <label >Account type <span>*</span></label>
                                            <div className="reg-radio">
                                                <span className="ref-spam">
                                                <label >School</label>
                                                    <input name="account" type="radio" value="" />
                                                </span>
                                                <span className="ref-spam">
                                                <label >Student</label>
                                                    <input name="account" type="radio" value="" />
                                                </span>
                                            </div>
                                            <button type="submit" className="log-submit-btn color-bg"  ><span>Register</span></button>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="log-separator fl-wrap"><span>or</span></div>
                        <div className="soc-log fl-wrap">
                            <p>For faster login or register use your social account.</p>
                            <a href="#" className="facebook-log"><i className="fab fa-facebook-f"></i>Connect with Facebook</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}