import React, { useState } from "react";
import TopStrip from "./TopStrip";

export default function Details(props) {
    return (
        <div>
            <TopStrip />
            <div className="details-header">
                <div className="details-cover-image"><img src={require("../images/bg/22.jpg")} /></div>
                <div className="details-profile">
                    <span>
                        <img src="https://upload.wikimedia.org/wikipedia/en/a/aa/Delhi_Public_School_Logo.png" />
                    </span>
                </div>
                <div className="details-map">
                    <span className="map-Icon"><i class="fa fa-map-marker-alt"></i></span>
                    <span className="rating-Number">4.3</span>
                    <span class="fa fa-star checked"></span>
                    <span class="fa fa-star checked"></span>
                    <span class="fa fa-star checked"></span>
                    <span class="fa fa-star checked"></span>
                    <span class="fa fa-star checked"></span>
                </div>
            </div>
            <div className="details-body">
                <ul>
                    <li>Overview</li>
                    <li>Fee Structure</li>
                    <li>Courses Provided</li>
                    <li>Photo</li>
                    <li>Scholarship</li>
                    <li>Other</li>
                </ul>
            </div>
        </div>
    )
} 