import React, { useState, useEffect, useRef } from "react";
import Navbar from "./components/Navbar";
import TopStrip from "./components/TopStrip";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";

import "./css/styles.css";
import "./css/color.css";
import "./css/plugins.css"
import "./css/cs-style.css";
// import "./css/invoice.css";
import "./css/reset.css";
import Home from "./components/Home";
import List from "./components/List";
import Registration from "./components/Registration";
import Filter from "./components/Filter";
import Details from "./components/Details";
import Main from "./view/Main";


export default function App() {
  const [state, setState] = useState({
    loader: true,
    show_reg: "none",
    details_one: {},
    top: "",
    height: "",
    scroll: ""
  });
  const refContainer = useRef({
    top: "",
    height: "",
    scroll: ""
  });
  useEffect(() => {
    setTimeout(() => {
      setState({ ...state, loader: false });
    }, 500);
    setState({
      top: refContainer.current.clientTop,
      height: refContainer.current.clientHeight
    });
    window.addEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    state.scroll > state.height / 2
      ? (document.body.style.paddingTop = `${state.top}px`)
      : (document.body.style.paddingTop = 0);
  })

  const handleScroll = () => {
    setState({ scroll: window.scrollY });
  };

  const Redirectx = ({ history }) => {
    history.push("/home");
    return <Main ref={refContainer} />;
  }
  return (
    <div className="App" id="main">
      {state.loader ? <div className="loader-wrap">
        <div className="pin">
          <div className="pulse"></div>
        </div>
      </div> : null}
      <header className="main-header" style={state.scroll >= 400 ? { background: "#3aaced" } : null}>
        <TopStrip showReg={(e) => setState({ show_reg: e })} />
        <Navbar />
      </header>
      <Registration show_reg={state.show_reg} close={() => setState({ ...state, show_reg: "none" })} />
      <Switch>
        <Route exact path="/" component={Redirectx} />
        <Route path="/home">
          <Main ref={refContainer} />
        </Route>
        <Route path="/details" component={Details} />
      </Switch>
    </div >
  );
}