import React, { useState } from "react";

export default function Home() {
    return (
        <section className="hero-section" data-scrollax-parent="true" id="sec1">
            <div className="hero-parallax">
                <div className="bg" data-bg="images/bg/22.jpg" data-scrollax="properties: { translateY: '200px' }"></div>
                <div className="overlay op7"></div>
            </div>
            <div className="hero-section-wrap fl-wrap">
                <div className="container">
                    <div className="main-search-input-wrap">
                        <div className="main-search-input fl-wrap">
                            <div className="main-search-input-item location" id="autocomplete-container">
                                <span className="inpt_dec"><i className="fal fa-map-marker"></i></span>
                                <input type="text" placeholder="Hotel , City..." className="autocomplete-input" id="autocompleteid2" value="" />
                                <a href="#"><i className="fal fa-dot-circle"></i></a>
                            </div>
                            <div className="main-search-input-item main-date-parent main-search-input-item_small">
                                <span className="inpt_dec"><i className="fal fa-calendar-check"></i></span> <input type="text" placeholder="When" name="main-input-search" value="" />
                            </div>
                            <div className="main-search-input-item">
                                <div className="qty-dropdown fl-wrap">
                                    <div className="qty-dropdown-header fl-wrap"><i className="fal fa-users"></i> Persons</div>
                                    <div className="qty-dropdown-content fl-wrap">
                                        <div className="quantity-item">
                                            <label><i className="fas fa-male"></i> Adults</label>
                                            <div className="quantity">
                                                <input type="number" min="1" max="3" step="1" value="1" />
                                            </div>
                                        </div>
                                        <div className="quantity-item">
                                            <label><i className="fas fa-child"></i> Children</label>
                                            <div className="quantity">
                                                <input type="number" min="0" max="3" step="1" value="0" />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <button className="main-search-button color2-bg" onclick="window.location.href='listing.html'">Search <i className="fal fa-search"></i></button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}