import React, { useState } from "react";

export default function List(props) {
    const [state, setState] = useState({
        list: [{ name: 'DPS Public School', status: 'Open', img: "https://upload.wikimedia.org/wikipedia/en/a/aa/Delhi_Public_School_Logo.png", location: "Delhi", rating: "4.2" },
        { name: 'Amity International School', status: 'Closed', img: "https://www.schoolmykids.com/wp-content/uploads/2018/06/AMITY-International-School-NOIDA.png", location: "Noida", rating: "3.5" },
        { name: 'DPS Public School', status: 'Open', img: "https://upload.wikimedia.org/wikipedia/en/a/aa/Delhi_Public_School_Logo.png", location: "Noida", rating: "4.5" },
        { name: 'Amity International School', status: 'Closed', img: "https://www.schoolmykids.com/wp-content/uploads/2018/06/AMITY-International-School-NOIDA.png", location: "Jaipur", rating: "4.0" },
        { name: 'DPS Public School', status: 'Open', img: "https://upload.wikimedia.org/wikipedia/en/a/aa/Delhi_Public_School_Logo.png", location: "Amritsar", rating: "4.3" },
        { name: 'Amity International School', status: 'Closed', img: "https://www.schoolmykids.com/wp-content/uploads/2018/06/AMITY-International-School-NOIDA.png", location: "Delhi", rating: "4.5" }],
        ratingList: []
    })
    const rating = (e) => {
        var ratingList = [];
        var len = parseInt(e);
        for (let index = 0; index < 5; index++) {
            if (index > len - 1) {
                ratingList.push(<span class="fa fa-star"></span>)
            }
            else {
                ratingList.push(<span class="fa fa-star checked"></span>)
            }
        }
        return ratingList;
    }
    return (
        <div class="listnerMain">
            {state.list.map(item => (
                <div className="list-contianer" onClick={() => props.history.push("/details")}>
                    <span><img src={item.img} className="list-img" /></span>
                    <span className="list-details">
                        <div><h2>{item.name}</h2></div>
                        <div>
                            <span className="list-rating">{item.rating}</span>
                            {rating(item.rating)}
                        </div>
                        <div><i class="fa fa-map-marker-alt"></i>
                            <span className="list-location">{item.location}</span></div>
                        <div>{item.status == "Open" || item.status == "open" ? <span className="list-open">Admission Open</span> : <span className="list-closed">Admission Closed</span>}</div>
                    </span>
                </div>
            ))}
        </div>
    )
}